.card {
  width: 200px;
  padding: 32px 16px 16px;
  box-sizing: border-box;
  border-radius: 5px;
  border: 1px solid $gray-elements;
  background: $white;

  @include hover(transform, scale(1.2));

  &__image {
    display: block;
    margin: 0 auto 40px;
    width: 160px;
    height: 134px;
  }

  &__title {
    margin-bottom: 4px;
    width: 100%;
    color: $main-accent;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
  }

  &:hover {
    & .card__title {
      color: $hover;
    }
  }

  &__code {
    margin-bottom: 16px;
  }

  &__review {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 24px;

    &-amount {
      color: $main-accent;
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;

    &-label {
      font-size: 12px;
      line-height: 18px;
    }

    &-amount {
      color: $main-accent;
      font-size: 16px;
      font-weight: 700;
      line-height: 18px;
    }
  }

  &__button {
    display: flex;
    justify-content: center;
    height: 40px;
    border-radius: 5px;
    border: 1px solid $blue-accent;
    background-color: $blue-accent;

    color: $white;
    align-items: center;
    font-size: 14px;
    line-height: 16px;
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;

    @include hover(color, $blue-accent);
    @include hover(background-color, $white);
  }
}
